.row {
  margin: 20px;
  color: #fff;
}

.posters {
  display: flex;
  padding: 20px;
  overflow-x: scroll;
  overflow-y: hidden;
}

.posters::-webkit-scrollbar {
  display: none;
}

.poster {
  max-height: 230px;
  margin-right: 15px;
  cursor: pointer;
}

.smallposter {
  max-height: 130px;
  margin-right: 10px;
  cursor: pointer;
}

.poster:hover {
  transform: scale(1.05);
}
